import React from 'react';
import { Button, Input } from 'antd';

const ActivityMathProblem = ({ handleFinish }) => {
    return (
        <div className="d-flex align-items-center justify-content-center">
            <img src="https://www.catalannews.com/cache/com_zoo/images/covid-deaths_0ad2195da689fd81592a52d87ee09db3.jpg" className="mb-2" style={{ width: 500 }} />
            <div className="ml-2">
                <Input placeholder="Reposta" />
                <Button onClick={handleFinish} className="mt-2">RESOLDRE PROVA</Button>
            </div>
        </div>

    )
}

export default ActivityMathProblem;